$calendar-box-size: 100px;
$calendar-box-gap: 1px;
$calendar-box-padding: 10px;
$days-count: 31;

$calendar-box-color: #eee;

$transition-time: 0.5s;

$calendar-size:
  ($calendar-box-size * 7) +
  ($calendar-box-padding * 6) +
  ($calendar-box-gap * 6);

$days:
  "mon" 0,
  "tue" 1,
  "wed" 2,
  "thu" 3,
  "fri" 4,
  "sat" 5,
  "sun" 6;
